import React from 'react'
import  {  useStaticQuery , graphql } from 'gatsby'
import ListingDescription from './ListingDescription.component'
import GoogleMapReact from 'google-map-react'
import FloorPlans from './FloorPlans.component'
import PropertyVideoVirtualTour from './PropertyVideoVirtualTour.component'
import PriceCalculator from './PriceCalculator.component'
import PropertyViews from './PropertyViews.component'
import Review from '../../Review.component'
import SimilarProperty from './SimilarProperties.component'
