import React from 'react'
import {Link} from 'gatsby'
import  {  useStaticQuery , graphql } from 'gatsby'
const ListedBy=()=> {
    const data = useStaticQuery(graphql`
		query{
			agent: file(relativePath: {eq: "team/lc1.png"}) {
				id
				childImageSharp {
				  fluid {
					originalImg
				  }
				}
              }}`
            )
    return (
        <div className="sidebar_advanced_search_widget">
			<div className="sl_creator">
				<h4 className="mb25">Listed By</h4>
				<div className="media">
					<Link to="/agent/single"><img className="mr-3" src={data.agent.childImageSharp.fluid.originalImg} alt="lc1.png"/></Link>
					<div className="media-body">
						<h5 className="mt-0 mb0">Samul Williams</h5>
						<p className="mb0">(123)456-7890</p>
						<p className="mb0">info@findhouse.com</p>
						<Link className="text-thm" to="/list">View My Listing</Link>
					</div>
				</div>
			</div>
			<ul className="sasw_list mb0">
				<li className="search_area">
					<div className="form-group">
						<input type="text" className="form-control" id="exampleInputName1ssd" placeholder="Your Name"/>
					</div>
				</li>
				<li className="search_area">
					<div className="form-group">
						<input type="number" className="form-control" id="exampleInputName2" placeholder="Phone"/>
					</div>
				</li>
				<li className="search_area">
					<div className="form-group">
						<input type="email" className="form-control" id="exampleInputEmail" placeholder="Email"/>
					</div>
				</li>
				<li className="search_area">
					<div className="form-group">
						<textarea id="form_message" name="form_message" className="form-control required" rows="5" required="required" placeholder="I'm interest in [ Listing Single ]"></textarea>
					</div>
				</li>
				<li>
					<div className="search_option_button">
						<button type="submit" className="btn btn-block btn-thm">Search</button>
					</div>
				</li>
			</ul>
		</div>
    )
}

export default ListedBy
