import React from 'react'
import { Row,Col,Nav,Tab } from 'react-bootstrap'
import {Line, Bar} from 'react-chartjs-2';
function PropertyViews() {
	const data={
	line:{
		labels: ["Jun 2019", "Jul 2019", "Aug 2019", "Sep 2019", "Oct 2019", "Nov 2019", "Dec 2019", "Jan 2020", "Feb 2020", "Mar 2020", "Apr 2020", "May 2020"],
	datasets: [
		{
		label: 'Dataset',
		fill: true,
		lineTension: 0.5,
		backgroundColor: '#ADD8E6',
		borderColor: '#0000FF',
		borderWidth: 2,
		data: [26.4, 39.8, 66.8, 66.4, 40.6, 55.2, 77.4, 69.8, 57.8, 76, 110.8, 142.6]
			}
  		]
	},
	line2:{
		labels:['January', 'February', 'March', 'April', 'May', 'June', 'July'],
            datasets: [{
                label: 'Dataset',
                borderColor: '#FF6384',
                backgroundColor: '#FF6384',
                data: [10, 30, 46, 2, 8, 50, 0],
                fill: false,
			}]
	},
	bar:{
		labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul"],
		datasets: [{
			label: "Dataset #1",
			backgroundColor: "rgba(255,99,132,0.2)",
			borderColor: "rgba(255,99,132,1)",
			borderWidth: 2,
			hoverBackgroundColor: "rgba(255,99,132,0.4)",
			hoverBorderColor: "rgba(255,99,132,1)",
			data: [65, 59, 20, 81, 56, 55, 40],
		}]
	}
}
    return (
		<div className="application_statics shop_single_tab_content style3 mt30">
	<Tab.Container defaultActiveKey="hourly">
		<Row>
			<Col sm={12}>
				<Nav as="ul" id="pills-tab2" className="nav nav-tabs float-right fn-414">
					<Nav.Item as="li">
						<Nav.Link eventKey="hourly">Hours</Nav.Link>
					</Nav.Item>
					<Nav.Item as="li">
						<Nav.Link eventKey="weekly">Weekly</Nav.Link>
					</Nav.Item>
					<Nav.Item as="li">
						<Nav.Link eventKey="monthly">Monthly</Nav.Link>
					</Nav.Item>
				</Nav>
			</Col>
			<Col sm={12}>
				<Tab.Content>
					<Tab.Pane eventKey="hourly">
						<h4 className="mt10-414">Property Views</h4>
						<Line
							data={data.line}
							options={{
								legend:{
								display:true,
								position:'top'
								}
							}}
							/>
					</Tab.Pane>
					<Tab.Pane eventKey="weekly">
						<h4 className="mt10-414">Property Views</h4>
						<Line
							data={data.line2}
							options={{
								responsive: true,
								title: {
									display: true,
									text: 'Sample tooltip with border'
								},
								tooltips: {
									position: 'nearest',
									mode: 'index',
									intersect: false,
									yPadding: 10,
									xPadding: 10,
									caretSize: 8,
									backgroundColor: 'rgba(72, 241, 12, 1)',
									titleFontColor: '#6F6F6F',
									bodyFontColor: '#6F6F6F',
									borderColor: 'rgba(0,0,0,1)',
									borderWidth: 4
								}}}
							/>
					</Tab.Pane>
					<Tab.Pane eventKey="monthly">
						<h4 className="mt10-414">Property Views</h4>
						<Bar
							data={data.bar}
							options={{
								 legend:{
								display:true,
								position:'top'
								}
								}
							}
							/>
					</Tab.Pane>
				</Tab.Content>
			</Col>
		</Row>
	</Tab.Container>
	</div>
    )
}

export default PropertyViews
