import React, {useState} from 'react'
import  {  useStaticQuery , graphql } from 'gatsby'
import { Nav, Tab } from 'react-bootstrap'
import ModalVideo from 'react-modal-video'
import 'react-modal-video/scss/modal-video.scss';
import styled from 'styled-components'
const IconConntainer= styled.div `
    background-color: rgb(255, 90, 95);
    border-radius: 50%;
    color: #ffffff;
    font-size: 22px;
    height: 70px;
    left: 0;
    line-height: 70px;
    margin: 0 auto;
    position: absolute;
    right: 0;
    text-align: center;
    top: 40%;
    width: 70px;
    z-index: 99;
    &:after{
        background-color: rgb(255, 90, 95, 0.2);
        border-radius: 100%;
        content: "";
        height: 90px;
        left: -10px;
        position: absolute;
        top: -10px;
        width: 90px;
        z-index: -1;
    }
`
function PropertyVideoVirtualTour() {
      const [isOpen, setOpen] = useState(false)
      const [isOpen2, setOpen2] = useState(false)

     const data = useStaticQuery(graphql`
		query{
			bg: file(relativePath: {eq: "background/7.jpg"}) {
				id
				childImageSharp {
				  fluid {
					originalImg
				  }
				}
			  }
		}`
    )
    return (
        <div className="shop_single_tab_content style2 bdr1 mt30" id='Video'>
            <Tab.Container defaultActiveKey="pv">
                <Nav as='ul' className="nav nav-tabs">
                    <Nav.Item as="li" className="nav-item">
                        <Nav.Link className="nav-link"eventKey="pv">Property Vieo</Nav.Link>
                    </Nav.Item>
                    <Nav.Item as="li" className="nav-item">
                        <Nav.Link className="nav-link" eventKey="vt">Virtual Tour</Nav.Link>
                    </Nav.Item>
                </Nav>
                <Tab.Content>
                    <Tab.Pane eventKey="pv" className=" row pl15 pl0-1199 pr15 pr0-1199">
                        <div className="property_video">
                            <div className="thumb">
                                <img className="pro_img img-fluid w100" src={data.bg.childImageSharp.fluid.originalImg}  alt="7.jpg"/>
                                <div className="overlay_icon">
                                    <ModalVideo channel='youtube' autoplay isOpen={isOpen2} videoId="oqNZOOWF8qM" onClose={() => setOpen2(false)} />
                                    <IconConntainer className="video_popup_btn red" onClick={()=> setOpen2(true)}>
                                        <span className="flaticon-play"></span>
                                    </IconConntainer>
                                </div>
                            </div>
                        </div>
                    </Tab.Pane>
                    <Tab.Pane eventKey="vt" className=" row pl15 pl0-1199 pr15 pr0-1199">
                        <div className="property_video">
                            <div className="thumb">
                                <img className="pro_img img-fluid w100" src={data.bg.childImageSharp.fluid.originalImg} alt="7.jpg"/>
                                <div className="overlay_icon">
                                    <ModalVideo channel='youtube' autoplay isOpen={isOpen} videoId="oqNZOOWF8qM" onClose={() => setOpen(false)} />
                                    <IconConntainer className="video_popup_btn red popup-youtube" onClick={()=> setOpen(true)}>
                                        <span className="flaticon-play"></span>
                                    </IconConntainer>
                                </div>
                            </div>
                        </div>
                    </Tab.Pane>
                </Tab.Content>
            </Tab.Container>
        </div>
    )
}

export default PropertyVideoVirtualTour
