import React,{useContext} from 'react'
import  {  useStaticQuery , graphql } from 'gatsby'
import {Accordion, AccordionContext , Card, useAccordionToggle} from 'react-bootstrap'


function ContextAwareToggle({ children, eventKey, callback }) {
	const currentEventKey = useContext(AccordionContext);
  
	const decoratedOnClick = useAccordionToggle(
	  eventKey,
	  () => callback && callback(eventKey),
	);
  
	const isCurrentEventKey = currentEventKey === eventKey;
  
	return (
		<Card.Header 
			onClick={decoratedOnClick}
			className={isCurrentEventKey ? 'active' : ''}
			>
		<div className="mb-0">
			<div className="btn btn-link" role="button" >{children}</div>
		</div>
	</Card.Header>
	);
  }
function FloorPlans() {
    const data = useStaticQuery(graphql`
		query{
			floorplan: file(relativePath: {eq: "resource/floor_plan.png"}) {
				id
				childImageSharp {
				  fluid {
					originalImg
				  }
				}
              }
              
		}`
    )
    
    return (
        <div className="application_statics mt30" id='plans'>
            <h4 className="mb30">Floor plans</h4>
                <div className="faq_according style2">
                <Accordion defaultActiveKey="2">
                    <Card className="floor_plan">
                        <ContextAwareToggle eventKey="0">
                            <ul className="mb0 text-left">
                                <li className="list-inline-item">First Floor</li>
                                <li className="list-inline-item"><p>Size:</p> <span>1267 Sqft</span></li>
                                <li className="list-inline-item"><p>Rooms:</p> <span>670 Sqft</span></li>
                                <li className="list-inline-item"><p>Baths:</p> <span>530 Sqft</span></li>
                                <li className="list-inline-item"><p>Price:</p> <span>$1489</span></li>
                            </ul>
                        </ContextAwareToggle>
                        <Accordion.Collapse eventKey="0">
                        <Card.Body className="text-center">
                            <div>
                                <img className="img-fluid" src={data.floorplan.childImageSharp.fluid.originalImg} alt="floor_plan.png"/>
                                <p>Plan description. Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat.</p>
                            </div>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card className="floor_plan">
                        <ContextAwareToggle eventKey="1">
                            <ul className="mb0 text-left">
                                <li className="list-inline-item">First Floor</li>
                                <li className="list-inline-item"><p>Size:</p> <span>1267 Sqft</span></li>
                                <li className="list-inline-item"><p>Rooms:</p> <span>670 Sqft</span></li>
                                <li className="list-inline-item"><p>Baths:</p> <span>530 Sqft</span></li>
                                <li className="list-inline-item"><p>Price:</p> <span>$1489</span></li>
                            </ul>
                        </ContextAwareToggle>
                        <Accordion.Collapse eventKey="1">
                        <Card.Body className="text-center">
                            <div>
                                <img className="img-fluid" src={data.floorplan.childImageSharp.fluid.originalImg} alt="floor_plan.png"/>
                                <p>Plan description. Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat.</p>
                            </div>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                </Accordion>
            </div>
        </div>
    )
}

export default FloorPlans
