import React from 'react'
import AdvanceSearchWidget from '../../../App/SidebarWidgets/AdvancedSearchWidget.component'
import CategoriesProperty from '../../CategoriesProperty.component'
import SidebarFeatured from '../../SideBarFeaturedListing.component'
import GridFeaturedProperties from '../Grid/GridFeaturedProperties.component'
import ListedBy from './ListedBy.component'

const SidebarComponents=() =>{
    return (
        <div>
            <div className="sidebar_listing_list">
                <ListedBy/>
            </div>
            <div className="sidebar_listing_list">
                <div className="sidebar_advanced_search_widget">
                    <AdvanceSearchWidget index="esdfd"/>
                </div>
            </div>
            <GridFeaturedProperties/>
            <CategoriesProperty/>
            <SidebarFeatured HeaderTitle="Recently Viewed"/>
		</div>
    )
}

export default SidebarComponents
